.error-wrap {
	width:500px;
}
.error-wrap img {
	width: 100%;
}

.login-wrap {
	width: 100%;
	max-width: 500px;
	margin: 100px auto;
}
.login-color {
	color: #2f8a31;
}
#login-btn {
	width: 100%;
}
#login-btn:focus,#login-btn:active {
	outline:none;
	border-color: transparent;
}

.w-h {
   width: 100%;
   height: 100%;
}
#data-container {
  float: left;
  background: #f8f8f8;
  padding: calc(52px + 40px) 0 0 250px;
  overflow-x: auto;
}
.data-wrap {
  overflow: hidden;
}

.left-side {
	width: 250px;
	height: 100%;
	top: 52px;
	left: 0;
	border-right: 1px solid #dad8d8;
	background: #F5F5F5;
	z-index: 2;
}
.left-bar-side {
	width: 30%;
	background: #5e5e5e;
	height: 100%;
	overflow: auto;
}
.left-bar-side ul .item {
	font-size: 16px;
	color: #fff;
	height: 60px;
	padding-top: 10px;
}
.left-bar-side ul .item:hover {
	background: #6e6e6e;
}
.left-bar-side ul .item.list-item-active {
	background: #808080;
}
.left-bar-side ul .item .item-desc {
	line-height: 25px;
	font-size: 14px;
	cursor: default;
}
.right-bar-side {
	width: 70%;
	color: #6b6b6b;
	height: 100%;
	overflow: auto;
	padding-bottom: 100px;
}
.r-bar-block .header-sub-list li {
	height: 40px;
	line-height: 40px;
	text-indent: 30px;
}
.r-bar-block .header-sub-list li:hover {
	background: #e7e7e7;
}
.r-bar-block .header-sub-list li .router-link-active {
	background: #d5d5d5;
}
.r-bar-block .header-sub-list li a {
	display: block;
	width: 100%;
	height: 100%;
	color: #6b6b6b;
}
a:hover {
	text-decoration: none;
}	

.navbar {
	margin-bottom: 0px;
	width: 100%;
	left: 0;
	top: 0;
	z-index: 2;
}
.dropdown-menu {
	right: 0;
	left: unset;
}
.navbar-options {
	margin-right: 20px;
}

